@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 0.2s ease;
}
*::-webkit-scrollbar {
  width: 9px;
  height: 8px;
  background-color: #dfdfdf;
  border-radius: 10px;
}
@media only screen and (max-width: 600px) {
  *::-webkit-scrollbar {
    width: 8px;
    height: 6px;
  }
}
*::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, rgba(36, 63, 214, 0.8) 0, rgba(52, 169, 247, 0.8) 100%);
  border-radius: 10px;
}
*::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #243fd6 0, #34a9f7 100%);
}

.react-tooltip {
  transition: all 0.2s ease !important;
}

.App {
  min-height: 110vh;
  display: flex;
  flex-direction: column;
}

.Toastify__toast-body {
  font-family: "Nunito", sans-serif;
}

body {
  font-family: "Nunito", sans-serif;
  background: rgb(217, 222, 247);
  background: linear-gradient(180deg, rgba(141, 152, 207, 0.28) 0, rgb(240, 243, 255) 100%);
  color: #343434;
}

.disable-scroll {
  overflow: hidden;
}

.skeleton {
  background-color: #bebebe;
  -webkit-box-shadow: 0px 0px 15px -3px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 15px -3px rgba(0, 0, 0, 0.2);
  height: 355px;
  width: 90%;
  margin: 10px auto;
  border-radius: 10px;
  border-radius: 10px;
  padding: 10px;
  animation: infiniteAnimation 2s infinite;
}
.skeleton div {
  width: 100%;
  margin: 0 auto;
  height: 30px;
  background-color: #ebebeb;
  border-radius: 10px;
  margin-bottom: 10px;
  background-color: rgba(150, 146, 146, 0.4862745098);
}
.skeleton div:nth-child(1) {
  height: 75%;
  margin: 0 auto 10px auto;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.skeleton div:nth-child(3) {
  width: 70%;
  margin: 0 auto;
}

/* hide arrows
 Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
  margin: 0 !important;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield !important;
}

@keyframes infiniteAnimation {
  0%, 100% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
}
small {
  opacity: 0.8;
}

.navlink-active {
  background-color: rgba(255, 255, 255, 0.115);
}

.custom-navbar {
  width: 100%;
  background-color: rgb(53, 66, 134) !important;
  background: linear-gradient(120deg, #354286 0%, #2793a7 100%);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 10px 67px;
  color: #fff;
  z-index: 999;
  backdrop-filter: none !important;
}
.custom-navbar .navbar-brand {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}
.custom-navbar .navbar-brand p {
  color: white;
  margin-bottom: 0;
}
.custom-navbar img {
  width: 40px;
}
.custom-navbar .container-fluid .navbar-toggler {
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.custom-navbar .container-fluid .navbar-toggler:focus {
  outline: none;
  box-shadow: none;
}
.custom-navbar .container-fluid .navbar-toggler:hover {
  transform: scale(1.1);
}
.custom-navbar .container-fluid .navbar-toggler img {
  display: none;
  width: 20px;
}
@media only screen and (max-width: 991px) {
  .custom-navbar .container-fluid .navbar-toggler img {
    display: block;
  }
}
.custom-navbar .navbar-nav {
  margin-left: auto;
  width: 100%;
}
.custom-navbar .navbar-nav li {
  margin-right: 43px;
  border-radius: 5px;
  transition: all 0.2s;
  display: flex;
  align-items: center;
}
@media only screen and (max-width: 991px) {
  .custom-navbar .navbar-nav li {
    margin-right: 0;
  }
}
.custom-navbar .navbar-nav li img {
  width: 20px;
}
.custom-navbar .navbar-nav li a {
  color: #fff !important;
  font-size: 16px;
  font-weight: 600;
  padding: 3px 10px;
  border-radius: 10px;
  transition: all 0.2s;
}
.custom-navbar .navbar-nav li a:hover {
  background: rgba(255, 255, 255, 0.11);
  transform: scale(1.1);
}
.custom-navbar .navbar-nav li .user-icon-link {
  display: flex;
  align-items: center;
}
.custom-navbar .navbar-nav .navbar-middle-part {
  margin-right: auto;
  margin-left: auto;
  display: flex;
}
@media only screen and (max-width: 991px) {
  .custom-navbar .navbar-nav .navbar-middle-part {
    flex-direction: column;
    align-items: center;
  }
}
@media only screen and (max-width: 991px) {
  .custom-navbar .navbar-nav .navbar-middle-part .nav-item {
    margin: 0 0 5px 0;
  }
}
.custom-navbar .navbar-nav .navbar-middle-part .nav-item a {
  border-radius: 5px;
}
.custom-navbar .navbar-nav .input-nav-link {
  margin-right: 0;
}
.custom-navbar .navbar-nav .input-nav-link:hover {
  background: none;
}
@media only screen and (max-width: 991px) {
  .custom-navbar .navbar-nav .input-nav-link {
    justify-content: center;
  }
}
@media only screen and (max-width: 550px) {
  .custom-navbar {
    padding: 10px;
  }
}

.nav-search-wrapper {
  position: relative;
  width: 100%;
  max-width: 231px;
}
.nav-search-wrapper form {
  position: relative;
}
.nav-search-wrapper input {
  background-color: #fff;
  border: none;
  outline: none;
  color: #455cd9;
  padding: 5px 20px 5px 10px;
  border-radius: 10px;
  font-size: 14px;
  width: 100%;
}
.nav-search-wrapper input:focus {
  outline: none;
}
.nav-search-wrapper input::placeholder {
  color: #455cd9;
}
@media only screen and (max-width: 450px) {
  .nav-search-wrapper input::placeholder {
    color: transparent;
  }
}
.nav-search-wrapper img {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  width: 17px;
}
.nav-search-wrapper .loader {
  width: 15px;
  height: 15px;
  border: 2px solid #576cdc;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  position: absolute;
  right: 3px;
  top: 8px;
}
@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.search-nav-right li {
  width: 100%;
}
.search-nav-right li .nav-search-wrapper {
  max-width: 100%;
}

.profile-link {
  position: relative;
  text-align: center;
}
@media only screen and (max-width: 991px) {
  .profile-link {
    width: 10%;
    margin: 4px auto !important;
    display: flex;
    flex-direction: column;
  }
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.profile-link .dropdown {
  animation: fade 0.3s ease-in-out;
  box-shadow: -2px 8px 50px 8px rgba(0, 0, 0, 0.35);
  -webkit-box-shadow: -2px 8px 50px 8px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: -2px 8px 50px 8px rgba(0, 0, 0, 0.35);
  height: 70px;
  width: 120px;
  border-radius: 10px;
  position: absolute;
  z-index: 10;
  transform: translateY(100%) translateX(-30%);
  background: rgba(46, 110, 152, 0.404);
  background-color: rgba(46, 110, 152, 0.404);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(10px);
}
.profile-link .dropdown button {
  border: none;
  background-color: transparent;
  color: #ffffff;
  padding: 5px 10px;
  border-radius: 10px;
}
.profile-link .dropdown button:hover {
  transform: scale(1.1);
  background: rgba(255, 255, 255, 0.11);
}
.profile-link .dropdown ul {
  margin: 0;
  padding: 0;
}
.profile-link .dropdown a {
  text-decoration: none;
}
.profile-link .dropdown a:hover {
  background: rgba(255, 255, 255, 0.11);
  transform: scale(1.15);
}
.profile-link .dropdown a span {
  color: #ffffff !important;
}
.profile-link .dropdown li {
  height: 28px;
  margin: 5px auto;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media only screen and (max-width: 991px) {
  .profile-link .dropdown {
    display: flex;
    justify-content: center;
    position: static;
    transform: translate(0);
    margin-bottom: 5px;
  }
}

.sticky-nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  z-index: 999;
  background: rgba(46, 110, 152, 0.404);
  background-color: rgba(46, 110, 152, 0.404) !important;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(20px) !important;
  transition: all 0.3s;
}

.nav-wrapper {
  height: 70px;
}

.Footer {
  background-color: #efefef;
  margin-top: auto;
  min-height: 40%;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
.Footer .sponsor-scroll::after {
  content: "";
  display: block;
  width: 90%;
  border-bottom: solid 1px rgba(69, 92, 217, 0.5);
  margin: 0 auto;
  padding-bottom: 1%;
}
.Footer .sponsor-scroll {
  min-height: 30%;
  padding: 2% 0;
}
@keyframes slide {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}
.Footer .sponsor-scroll .logos {
  overflow: hidden;
  height: 50px;
  white-space: nowrap;
  position: relative;
}
.Footer .sponsor-scroll .logos:before,
.Footer .sponsor-scroll .logos:after {
  position: absolute;
  top: 0;
  width: 20%;
  height: 100%;
  content: "";
  z-index: 2;
}
.Footer .sponsor-scroll .logos:before {
  left: 0;
  background: linear-gradient(to left, rgba(255, 255, 255, 0), #efefef);
}
.Footer .sponsor-scroll .logos:after {
  right: 0;
  background: linear-gradient(to right, rgba(255, 255, 255, 0), #efefef);
}
.Footer .sponsor-scroll .logos:hover .logos-slide {
  animation-play-state: paused;
}
.Footer .sponsor-scroll .logos-slide {
  display: inline-block;
  animation: 25s slide infinite linear;
}
.Footer .sponsor-scroll .logos-slide img {
  height: 50px;
  margin: 0 50px;
}
.Footer .info-panel {
  max-height: 70%;
  padding: 2% 0%;
  overflow: hidden;
  margin: 0 auto;
  display: grid;
  width: 100%;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: auto;
  align-items: center;
  justify-items: center;
  gap: 15px;
}
@media (max-width: 767px) {
  .Footer .info-panel {
    transform: scale(0.9);
    grid-template-columns: repeat(2, 1fr);
    gap: 50px;
  }
}
.Footer .info-panel ul {
  font-weight: 600;
  text-align: center;
  font-size: 100%;
  color: #343434;
  padding: 0;
  margin: 0;
  list-style-type: none;
  width: 100%;
}
.Footer .info-panel .social-icons {
  display: grid;
  gap: 10px;
  justify-content: space-around;
}
@media (max-width: 767px) {
  .Footer .info-panel .social-icons {
    width: 80%;
    gap: 15px;
    grid-template-columns: repeat(4, 1fr);
    grid-column: 1/-1;
  }
}

